import React , { useState } from "react";
import { Link } from "react-router-dom";
import {AiOutlineBars} from "react-icons/ai";

function Navbar() {
  const [showMenu, setshowMenu] = useState('md:hidden');
  const [justification, setjustification] = useState('mx-4');
  const MenuOnClick =()=>{
    if (showMenu==='md:hidden'){
      setshowMenu('flex-col  w-full')
      console.log("menuclicked")
      setjustification("my-2")

    }
    else{
      setshowMenu('md:hidden')
      setjustification("mx-4")

    }


  }
  const menueItems = [
    {
      title: "Home",
      key: "/",
    },
    {
      title: "Projects",
      key: "/projects",
    },
    {
      title: "Product",
      key: "/products",
    },
    {
      title: "Contactsmilf",
      key: "/contact",
    },
  ];
  const pathname = window.location.pathname
  return (
    <div className="font-super fixed inset-0 z-50">
      <div className={`flex bg-theme_color text-white  justify-between items-center p-2 shadow-lg  ${showMenu!=="md:hidden"&&  'flex-col'}  `}>
        <div className="flex justify-between w-full">
          <h1 className="text-4xl font-semibold ">ASHSTAR</h1>
          <button onClick={MenuOnClick}className=" md:flex lg:hidden xl:hidden 2xl:hidden "><AiOutlineBars size={40}/></button>
          
        </div>
        <div className={`flex  items-start  ${showMenu}`}>
          {menueItems.map((item) => {
            return (
              <li key={item.key} className={`list-none ${justification} p-0 ${item.key===pathname && 'bg-white text-black rounded-md'}`}>
                <Link to={`${item.key}`}>{item.title}</Link>
              </li>
            );
          })}
        </div>

      </div>
    </div>
  );
}

export default Navbar;
